import back from '@/components/back'
import noData from '@/components/noData'

export default {
    components:{
        back,
        noData
    },
    data(){
        return {
            page: 1,
            haveMore: true,
            list: [],
            user: this.$store.state.userInfo,
            pageShow: false,
            start: '',
            end: '',
            mOrD: false,
            month: '',
            flag: false,
            showTime:false,
            StartCreateTime: '',
            EndCreateTime: '',
        }
    },
    created(){
        const date = new Date()
        const year = date.getFullYear()
        let month = date.getMonth()+1
        let day = date.getDate()
        if(month < 10){
            month = `0${month}`
        }
        if(day < 10){
            day = `0${day}`
        }
        this.month = `${year}-${month}`
        this.pull(false)
    },
    mounted(){
        this.getMore()
    },
    beforeDestroy(){
        window.onscroll = null
    },
    methods:{
        getMore(){
            const el = this.$refs.el
            let timer = null
            window.onscroll = () => {
                clearTimeout(timer)
                timer = setTimeout(() => {
                    const top = document.documentElement.scrollTop
                                || window.pageYOffset
                                || document.body.scrollTop,
                            h = el.scrollHeight,
                            wh = window.innerHeight

                    if (h - top - wh < 100 && this.haveMore){
                        this.page += 1
                        this.pull(true)
                    }
                }, 100)
            }
        },
        pull(h){
            this.$post('PlatFormAPI/Doctor/GetDoctorIntegralRecordByDrId',this.$pick({
                DrId: this.user.Data.User.DoctorId,
                IntegralTypeId: 0,
                StartCreateTime: this.StartCreateTime,
                EndCreateTime: this.EndCreateTime,
                PageIndex: this.page,
                PageSize: 15,
            })).then(o => {
                this.pageShow = true
                if (!o.ReturnData || o.ReturnData.length < 15){
                    this.haveMore = false
                }
                else {
                    this.haveMore = true
                }
                
                if (h){
                    o.ReturnData.forEach(v => {
                        this.list.push(v)
                    })
                }
                else {
                    this.list = o.ReturnData
                }
            })
        },
        changeType(){
            this.mOrD = !this.mOrD
        },
        search(){
            this.page = 1
            this.haveMore = true
            this.showTime = false
            if(!this.mOrD){
                this.StartCreateTime = this.month+'-01'
                const arr = this.month.split('-')
                var lastDay= new Date(arr[0],arr[1],'0');
                this.EndCreateTime = `${lastDay.getFullYear()}-${lastDay.getMonth()+1}-${lastDay.getDate()}`
            }
            else{
                this.StartCreateTime = this.start
                this.EndCreateTime = this.end
            }
            this.pull(false)
        }
    }
}



